.contents-Education {
  padding: 50px 15px 20px 15px;
  border-bottom: 1px solid #dcd9d9;
  text-align: center;
  background: #f2f2f5;
}

.contents-Education h2 {
  color: #374054;
  margin-bottom: 50px;
}

.Education-heading {
  position: relative;
  display: inline-block;
  font-size: 2em;
  font-weight: 300;
  margin: 0 0 30px 0;
  padding-bottom: 10px;
}

.Education-heading:after {
  position: absolute;
  content: "";
  top: 100%;
  height: 1px;
  width: 50px;
  left: 0;
  right: 0;
  margin: 0 auto;
  background: #3498db;
}

.Education-block {
  max-width: 60em;
  margin: 0 auto 30px auto;
  padding: 15px;
  border: 1px solid #dcd9d9;
  text-align: left;
  display: flex;
  background: white;
}

.logo {
  padding: 50px;
  width: 100px;
}

.Education-contents {
  padding: 30px;
}

.Education-contents h3 {
  font-weight: 500;
  float: left;
  margin: 0;
  color: #374054;
}

.Education-contents span {
  color: #74808a;
  float: right;
}

.Education-contents h4 {
  color: #74808a;
  clear: both;
  font-weight: 500;
  margin: 0 0 15px 0;
}

.Education-contents p {
  margin: 0;
  color: #74808a;
  font-size: 0.9em;
  line-height: 20px;
}

@media only screen and (max-width: 992px) {
  .Education-heading {
    color: #374054;
    position: relative;
    display: inline-block;
    font-size: 4em;
    font-weight: 300;
    margin: 0 0 50px 0;
    padding-bottom: 10px;
  }

  .Education-heading:after {
    position: absolute;
    content: "";
    top: 100%;
    height: 1px;
    width: 150px;
    left: 0;
    right: 0;
    margin: 0 auto;
    background: #3498db;
  }

  .Education-block {
    max-width: 100%;
    margin: 0 auto 30px auto;
    padding: 15px;
    border: 1px solid #dcd9d9;
    text-align: left;
  }

  .Education-univImg {
    padding: 30px 0px 30px 30px;
  }

  .Education-contents h3 {
    font-weight: 500;
    float: left;
    margin: 0;
    color: #374054;
    font-size: 4em;
    padding: 10px;
  }

  .Education-contents span {
    color: #74808a;
    float: left;
    font-size: 3em;
    padding: 10px;
  }

  .Education-contents h4 {
    color: #74808a;
    clear: both;
    font-weight: 500;
    margin: 0 0 15px 0;
    font-size: 2em;
    padding: 10px;
  }

  .Education-contents p {
    line-height: 1.5em;
    font-size: 2.5em;
    padding: 10px;
  }
}